import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import bridge from "@vkontakte/vk-bridge";
import { useGlobalState } from '../index';
import { Panel, Group, Header, SimpleCell, RichCell, InfoRow, Avatar, Accordion, Div, Snackbar } from '@vkontakte/vkui';
import { Icon28User } from '@vkontakte/icons';
import { lastStudentsScheduleUpdate, lastTeachersScheduleUpdate, lastEmployeesInformationUpdate } from '../utils/Database';

const Information = ({ id }) => {
    const [popout, setPopout] = useGlobalState('popout');

    const infoStyle = { color: 'var(--vkui--color_text_subhead)' };
    const [group, setGroup] = useState('');
    const [teacher, setTeacher] = useState('');
    const [author, setAuthor] = useState({});

    function setGetErrorPopout() {
        setPopout(
            <Snackbar
                onClose={() => setPopout(null)}
                duration={3000}
                before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
            >Не удалось получить пользовательские данные</Snackbar>
        );
    };

    useEffect(() => {
        async function getData() {
            bridge.send('VKWebAppStorageGet', { keys: ['scheduleStudents']}).then((data) => {
                if (data.keys) {
                    setGroup(JSON.parse(data.keys[0].value)[3]);
                } else {
                    setGetErrorPopout();
                };
            }).catch((error) => {
                setGetErrorPopout();
            });
            bridge.send('VKWebAppStorageGet', { keys: ['scheduleTeachers']}).then((data) => {
                if (data.keys) {
                    setTeacher(JSON.parse(data.keys[0].value)[0]);
                } else {
                    setGetErrorPopout();
                };
            }).catch((error) => {
                setGetErrorPopout();
            });
            bridge.send('VKWebAppGetUserInfo', { user_id: 480989368 }).then((data) => {
                if (data.id) {
                    setAuthor({url: "https://vk.com/id" + data.id, avatar: data.photo_100, first_name: data.first_name, last_name: data.last_name});
                } else {
                    setGetErrorPopout();
                };
            }).catch((error) => {
                setGetErrorPopout();
            });
		}
        getData();
    }, []);

    return (
        <Panel id={id}>
            <Group header={<Header mode="secondary">Пользовательская информация</Header>}>
                <SimpleCell multiline>
                    <InfoRow header="Выбранная группа">{group.length <= 1 ? "-" : group}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Выбранный преподаватель">{teacher.length <= 1 ? "-" : teacher}</InfoRow>
                </SimpleCell>
            </Group>
            <Group header={<Header mode="secondary">Техническая информация</Header>}>
                <SimpleCell multiline>
                    <InfoRow header="Версия приложения">1.3.2</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление расписания студентов">{lastStudentsScheduleUpdate()}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление расписания преподавателей">{lastTeachersScheduleUpdate()}</InfoRow>
                </SimpleCell>
                <SimpleCell multiline>
                    <InfoRow header="Последнее обновление информации о преподавателях">{lastEmployeesInformationUpdate()}</InfoRow>
                </SimpleCell>
            </Group>
            <Group header={<Header mode="secondary">Общая информация</Header>}>
                <Accordion>
                    <Accordion.Summary multiline iconPosition="before">Как часто обновляется расписание?</Accordion.Summary>
                        <Accordion.Content>
                            <Div style={infoStyle}>
                                Расписание обновляется из официального сайта БГТУ при наличии изменений, начале нового семестра или сессии.
                            </Div>
                        </Accordion.Content>
                </Accordion>
            </Group>
            {Object.keys(author).length > 0 && (
                <Group header={<Header mode="secondary">Контакты</Header>}>
                    <RichCell multiline
                        onClick={() => window.open(author.url, '_blank').focus()}
                        before={<Avatar size={48} noBorder fallbackIcon={<Icon28User/>} src={author.avatar}/>}
                        text="Разработчик приложения"
                        caption="Обращаться по любым вопросам"
                    >{author.first_name} {author.last_name}</RichCell>
                </Group>
            )}
        </Panel>
    );
};

Information.propTypes = {
    id: PropTypes.string.isRequired,
};

export default Information;