import fetch from 'node-fetch';
import dayjs from 'dayjs';
import isoWeek from 'dayjs/plugin/isoWeek';

dayjs.extend(isoWeek);

let scheduleStudents = {};
let scheduleTeachers = {};
let employeesInformation = {};

await fetch("https://uncaught1.github.io/bgtu-vk-mini-app-database/scheduleStudents.json", { method: "Get" }).then(res => res.json()).then((json) => {
   scheduleStudents = json;
});
await fetch("https://uncaught1.github.io/bgtu-vk-mini-app-database/scheduleTeachers.json", { method: "Get" }).then(res => res.json()).then((json) => {
   scheduleTeachers = json;
});
await fetch("https://uncaught1.github.io/bgtu-vk-mini-app-database/employeesInformation.json", { method: "Get" }).then(res => res.json()).then((json) => {
   employeesInformation = json;
});

