import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import bridge from "@vkontakte/vk-bridge";
import { useGlobalState } from '../index';
import { MiniInfoCell, Panel, FormItem, CustomSelect, Card, Header, Group, CardGrid, RichCell, SegmentedControl, Avatar, Snackbar } from '@vkontakte/vkui';
import { Icon20CalendarOutline, Icon20EducationOutline } from '@vkontakte/icons';
import { getWeekStatus, isEvenWeek, currentTeachersPeriod, findTeachers, findTeachersSchedule, getFaculties } from '../utils/Database';

const ScheduleTeachers = ({ id }) => {
    const [popout, setPopout] = useGlobalState('popout');

    const [week, setWeek] = useState(isEvenWeek());
    const [faculty, setFaculty] = useState();
    const [teacher, setTeacher] = useState();

    function setSetErrorPopout() {
        setPopout(
            <Snackbar
                onClose={() => setPopout(null)}
                duration={3000}
                before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
            >Не удалось сохранить пользовательские данные</Snackbar>
        );
    };

    function setGetErrorPopout() {
        setPopout(
            <Snackbar
                onClose={() => setPopout(null)}
                duration={3000}
                before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
            >Не удалось получить пользовательские данные</Snackbar>
        );
    };

    function storageSet(value) {
        bridge.send('VKWebAppStorageSet', { 
            key: 'scheduleTeachers',
            value: JSON.stringify([value]),
        }).then((data) => { 
            if (!data.result) {
                setSetErrorPopout();
            }
        }).catch((error) => {
            setSetErrorPopout();
        });
    };

    useEffect(() => {
        async function getData() {
            await bridge.send('VKWebAppStorageGet', { keys: ['scheduleTeachers']}).then((data) => {
                if (data.keys) {
                    setTeacher(JSON.parse(data.keys[0].value)[0]);
                } else {
                    setGetErrorPopout();
                };
            }).catch((error) => {
                setGetErrorPopout();
            });
        }
        getData();
    }, []);

    let teacherData = findTeachersSchedule(teacher);

    let countData = {day: "", count: 0};

    function returnLesson(key, day, lesson) {
        if (countData.day != day) countData.count = 0;
        countData.day = day;

        if (teacherData.schedule[day][lesson].week==="Всегда") {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={teacherData.schedule[day][lesson].time}
                        text={teacherData.schedule[day][lesson].type}
                        caption={teacherData.schedule[day][lesson].group}
                        after={teacherData.schedule[day][lesson].classroom}
                    >{teacherData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
        if (teacherData.schedule[day][lesson].week==="Нечётная" && !week) {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={teacherData.schedule[day][lesson].time}
                        text={teacherData.schedule[day][lesson].type}
                        caption={teacherData.schedule[day][lesson].group}
                        after={teacherData.schedule[day][lesson].classroom}
                    >{teacherData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
        if (teacherData.schedule[day][lesson].week==="Чётная" && week) {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={teacherData.schedule[day][lesson].time}
                        text={teacherData.schedule[day][lesson].type}
                        caption={teacherData.schedule[day][lesson].group}
                        after={teacherData.schedule[day][lesson].classroom}
                    >{teacherData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
    };

    return (
        <Panel id={id}>
            <Group>
                <MiniInfoCell mode="accent" textWrap="full" before={<Icon20CalendarOutline/>}>{getWeekStatus()}</MiniInfoCell>
                <MiniInfoCell mode="accent" textWrap="full" before={<Icon20EducationOutline/>}>Текущий период: {currentTeachersPeriod()}</MiniInfoCell>
            </Group>
            <Group>
                <FormItem top="Выберите тип недели для отображения">
                    <SegmentedControl
                        size="m"
                        value={week}
                        onChange={(value) => setWeek(value)}
                        options={[
                            {
                                label: 'Нечётная',
                                value: false,
                            },
                            {
                                label: 'Чётная',
                                value: true,
                            },
                        ]}
                    />
                </FormItem>
            </Group>
            <Group>
                <FormItem top="Факультет / Институт">
                    <CustomSelect multiline
                        value={faculty}
                        onChange={(e) => setFaculty(e.target.value)}
                        placeholder="Выберите факультет или институт"
                        options={getFaculties()}
                    />
                </FormItem>
                <FormItem top="Преподаватель">
                    <CustomSelect multiline
                        searchable
                        value={teacher}
                        onChange={(e) => {
                            setTeacher(e.target.value);
                            storageSet(e.target.value);
                        }}
                        placeholder="Введите ФИО преподавателя"
                        options={findTeachers(faculty)}
                    />
                </FormItem>
            </Group>
            {teacherData && Object.keys(teacherData.schedule).map((day, m) => (
                <Group header={<Header mode="primary" size="large">{day}</Header>} key={m}>
                    <CardGrid size="l">
                        {Object.keys(teacherData.schedule[day]).map((lesson, n) => (returnLesson(n, day, lesson)))}
                    </CardGrid>
                </Group>
            ))}
        </Panel>
    );
};

ScheduleTeachers.propTypes = {
    id: PropTypes.string.isRequired,
};

export default ScheduleTeachers;