import * as React from 'react';
import ReactDOM from 'react-dom/client';
import { createGlobalState } from 'react-hooks-global-state';
import vkBridge, { parseURLSearchParamsForGetLaunchParams } from '@vkontakte/vk-bridge';
import { useAppearance, useInsets, useAdaptivity } from '@vkontakte/vk-bridge-react';
import { ConfigProvider, AdaptivityProvider, AppRoot, ModalRoot, ModalCard, Spacing, Separator, Card, SimpleCell, MiniInfoCell, Snackbar, SplitLayout, Avatar } from '@vkontakte/vkui';
import { Icon28User, Icon28CheckCircleOutline, Icon28MailOutline, Icon28PhoneOutline, Icon20BookmarkOutline, Icon20BookOutline, Icon20EducationOutline } from '@vkontakte/icons';
import { transformVKBridgeAdaptivity } from './utils/transformVKBridgeAdaptivity';
import App from './App';

export const { useGlobalState } = createGlobalState({ popout: null, activeModal: null, employee: { name: "-", photo: "", phone: "-", email: "-", qualification: "-", faculty: "-", department: "-", post: "-" } });

vkBridge.send('VKWebAppInit');

const Root = () => {
    const vkBridgeAppearance = useAppearance() || undefined;
    const vkBridgeInsets = useInsets() || undefined;
    const vkBridgeAdaptivityProps = transformVKBridgeAdaptivity(useAdaptivity());
    const { vk_platform } = parseURLSearchParamsForGetLaunchParams(window.location.search);

    const [popout, setPopout] = useGlobalState('popout');
    const [activeModal, setActiveModal] = useGlobalState('activeModal');
    const [employee, setEmployee] = useGlobalState('employee');

    function copyToClipboard(text) {
        vkBridge.send('VKWebAppCopyText', {
            text: text
        }).then((data) => { 
            if (data.result) {
                setPopout(
                    <Snackbar
                        onClose={() => setPopout(null)}
                        duration={3000}
                        before={<Icon28CheckCircleOutline fill="var(--vkui--color_icon_positive)"/>}
                    >Скопировано в буфер обмена</Snackbar>
                );
            };
        });
    };

    const modalRoot = (
        <ModalRoot activeModal={activeModal}>
            <ModalCard
                id="employee"
                onClose={() => setActiveModal(null)}
                icon={<Avatar noBorder size={168} src={employee.photo} fallbackIcon={<Icon28User width={168} height={168}/>}/>}
                header={employee.name}
                subheader={<MiniInfoCell textWrap="full">{employee.post}</MiniInfoCell>}
            >
                <Spacing size={24}>
                    <Separator/>
                </Spacing>
                    <MiniInfoCell before={<Icon20EducationOutline/>} textWrap="full">{!employee.qualification || employee.qualification.length <= 1 ? "Квалификация" : employee.qualification}</MiniInfoCell>
                    <MiniInfoCell before={<Icon20BookmarkOutline/>} textWrap="full">{!employee.faculty || employee.faculty.length <= 1 ? "Факультет" : employee.faculty}</MiniInfoCell>
                    <MiniInfoCell before={<Icon20BookOutline/>} textWrap="full">{!employee.department || employee.department.length <= 1 ? "Кафедра" : employee.department}</MiniInfoCell>
                <Spacing size={24}>
                    <Separator/>
                </Spacing>
                <Card mode="outline">
                    <SimpleCell onClick={() => copyToClipboard(employee.email)} before={<Avatar noBorder size={48} fallbackIcon={<Icon28MailOutline/>}/>} subtitle="Почта">{employee.email}</SimpleCell>
                </Card>
                <Spacing size={12}/>
                <Card mode="outline">
                    <SimpleCell onClick={() => copyToClipboard(employee.phone)} before={<Avatar noBorder size={48} fallbackIcon={<Icon28PhoneOutline/>}/>} subtitle="Телефон">{employee.phone}</SimpleCell>
                </Card>
            </ModalCard>
        </ModalRoot>
    );

    return (
        <ConfigProvider
            appearance={vkBridgeAppearance}
            platform={vk_platform === 'desktop_web' ? 'vkcom' : undefined}
            isWebView={vkBridge.isWebView()}
            hasCustomPanelHeaderAfter={true}
            transitionMotionEnabled={false}
        >
            <AdaptivityProvider {...vkBridgeAdaptivityProps}>
                <AppRoot mode="full" safeAreaInsets={vkBridgeInsets}>
                    <SplitLayout popout={popout} modal={modalRoot}>
                        <App/>
                    </SplitLayout>
                </AppRoot>
            </AdaptivityProvider>
        </ConfigProvider>
    );
};

ReactDOM.createRoot(document.getElementById('root')).render(<Root/>);