import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import bridge from "@vkontakte/vk-bridge";
import { useGlobalState } from '../index';
import { MiniInfoCell, Panel, CustomSelect, FormItem, Card, Header, Group, CardGrid, RichCell, SegmentedControl, Avatar, Snackbar, ButtonGroup, Button } from '@vkontakte/vkui';
import { Icon20CalendarOutline, Icon20EducationOutline, Icon28ErrorCircleOutline } from '@vkontakte/icons';
import { getWeekStatus, isEvenWeek, currentStudentsPeriod, findGroups, findStudentsSchedule, findEmployeeInformation, getForms, getLevels, getFaculties } from '../utils/Database';

const ScheduleStudents = ({ id }) => {
    const [popout, setPopout] = useGlobalState('popout');
    const [activeModal, setActiveModal] = useGlobalState('activeModal');
    const [employee, setEmployee] = useGlobalState('employee');

    const [week, setWeek] = useState(isEvenWeek());
    const [form, setForm] = useState('Очная');
    const [level, setLevel] = useState();
    const [faculty, setFaculty] = useState();
    const [group, setGroup] = useState();

    function setSetErrorPopout() {
        setPopout(
            <Snackbar
                onClose={() => setPopout(null)}
                duration={3000}
                before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
            >Не удалось сохранить пользовательские данные</Snackbar>
        );
    };

    function setGetErrorPopout() {
        setPopout(
            <Snackbar
                onClose={() => setPopout(null)}
                duration={3000}
                before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
            >Не удалось получить пользовательские данные</Snackbar>
        );
    };

    function storageSet(value) {
        bridge.send('VKWebAppStorageSet', { 
            key: 'scheduleStudents',
            value: JSON.stringify([form, level, faculty, value]),
        }).then((data) => { 
            if (!data.result) {
                setSetErrorPopout();
            }
        }).catch((error) => {
            setSetErrorPopout();
        });
    };

    useEffect(() => {
        async function getData() {
            await bridge.send('VKWebAppStorageGet', { keys: ['scheduleStudents']}).then((data) => {
                if (data.keys) {
                    setForm(JSON.parse(data.keys[0].value)[0]);
                    setLevel(JSON.parse(data.keys[0].value)[1]);
                    setFaculty(JSON.parse(data.keys[0].value)[2]);
                    setGroup(JSON.parse(data.keys[0].value)[3]);
                } else {
                    setGetErrorPopout();
                };
            }).catch((error) => {
                setGetErrorPopout();
            });
        }
        getData();
    }, []);

    let groupData = findStudentsSchedule(form, group);

    let countData = {day: "", count: 0};

    function openEmployeeCard(name) {
        let employeeData = findEmployeeInformation(name);
        if (employeeData == null) {
            setPopout(
                <Snackbar
                    onClose={() => setPopout(null)}
                    duration={3000}
                    before={<Icon28ErrorCircleOutline fill="var(--vkui--color_icon_negative)"/>}
                >Не удалось получить данные преподавателя</Snackbar>
            );
        } else {
            setEmployee({ name: "-", photo: "", phone: "-", email: "-", qualification: "-", faculty: "-", department: "-", post: "-" });
            setEmployee(employeeData);
            setActiveModal("employee");
        };
    };

    function returnLesson(key, day, lesson) {
        if (countData.day != day) countData.count = 0;
        countData.day = day;

        const teacherList = groupData.schedule[day][lesson].teacher.split(" | ");
        const actions = (
            <ButtonGroup mode="horizontal" gap="s">
                {teacherList && Object.keys(teacherList).map((teacher, m) => (
                    <Button key={m} appearance="neutral" mode="outline" size="s" onClick={() => openEmployeeCard(teacherList[teacher])}>{teacherList[teacher]}</Button>
                ))}
            </ButtonGroup>
        );

        if (groupData.schedule[day][lesson].week==="Всегда") {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={groupData.schedule[day][lesson].time}
                        text={groupData.schedule[day][lesson].type}
                        actions={actions}
                        after={groupData.schedule[day][lesson].classroom}
                    >{groupData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
        if (groupData.schedule[day][lesson].week==="Нечётная" && !week) {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={groupData.schedule[day][lesson].time}
                        text={groupData.schedule[day][lesson].type}
                        actions={actions}
                        after={groupData.schedule[day][lesson].classroom}
                    >{groupData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
        if (groupData.schedule[day][lesson].week==="Чётная" && week) {
            countData.count += 1;
            return (
                <Card mode="shadow" key={key}>
                    <RichCell multiline
                        before={<Avatar size={48} noBorder initials={countData.count}/>}
                        subhead={groupData.schedule[day][lesson].time}
                        text={groupData.schedule[day][lesson].type}
                        actions={actions}
                        after={groupData.schedule[day][lesson].classroom}
                    >{groupData.schedule[day][lesson].title}</RichCell>
                </Card>
            );
        };
    };

    return (
        <Panel id={id}>
            <Group>
                <MiniInfoCell mode="accent" textWrap="full" before={<Icon20CalendarOutline/>}>{getWeekStatus()}</MiniInfoCell>
                <MiniInfoCell mode="accent" textWrap="full" before={<Icon20EducationOutline/>}>Текущий период: {currentStudentsPeriod(form)}</MiniInfoCell>
            </Group>
            <Group>
                <FormItem top="Выберите тип недели для отображения">
                    <SegmentedControl
                        size="m"
                        value={week}
                        onChange={(value) => setWeek(value)}
                        options={[
                            {
                                label: 'Нечётная',
                                value: false,
                            },
                            {
                                label: 'Чётная',
                                value: true,
                            },
                        ]}
                    />
                </FormItem>
            </Group>
            <Group>
                <FormItem top="Форма обучения">
                    <CustomSelect multiline
                        value={form}
                        onChange={(e) => setForm(e.target.value)}
                        options={getForms()}
                    />
                </FormItem>
                <FormItem top="Уровень образования">
                    <CustomSelect multiline
                        value={level}
                        onChange={(e) => setLevel(e.target.value)}
                        placeholder="Выберите уровень образования"
                        options={getLevels()}
                    />
                </FormItem>
                <FormItem top="Факультет / Институт">
                    <CustomSelect multiline
                        value={faculty}
                        onChange={(e) => setFaculty(e.target.value)}
                        placeholder="Выберите факультет или институт"
                        options={getFaculties()}
                    />
                </FormItem>
                <FormItem top="Группа">
                    <CustomSelect multiline
                        value={group}
                        onChange={(e) => {
                            setGroup(e.target.value);
                            storageSet(e.target.value);
                        }}
                        placeholder="Выберите группу"
                        options={findGroups(form, level, faculty)}
                    />
                </FormItem>
            </Group>
            {groupData && Object.keys(groupData.schedule).map((day, m) => (
                <Group header={<Header mode="primary" size="large">{day}</Header>} key={m}>
                    <CardGrid size="l">
                        {Object.keys(groupData.schedule[day]).map((lesson, n) => (returnLesson(n, day, lesson)))}
                    </CardGrid>
                </Group>
            ))}
        </Panel>
    );
};

ScheduleStudents.propTypes = {
    id: PropTypes.string.isRequired,
};

export default ScheduleStudents;