import React, { useState } from 'react';
import { View, Group, SplitCol, SimpleCell, Avatar, Tabs, TabsItem, HorizontalScroll } from '@vkontakte/vkui';
import { Icon16EducationOutline, Icon16UserOutline, Icon16InfoOutline } from '@vkontakte/icons';
import '@vkontakte/vkui/dist/vkui.css';

import ScheduleStudents from './panels/ScheduleStudents';
import ScheduleTeachers from './panels/ScheduleTeachers';
import Information from './panels/Information';

import logo from './assets/logo.png';

const App = () => {
	const [activePanel, setActivePanel] = useState('scheduleStudents');

	return (
		<SplitCol>
			<Group>
				<SimpleCell multiline before={<Avatar src={logo} size={32}/>}>БГТУ Расписание</SimpleCell>
				<Tabs
					mode="accent" 
					layoutFillMode="auto" 
					withScrollToSelectedTab
					scrollBehaviorToSelectedTab="center"
				>
					<HorizontalScroll arrowSize="m">
						<TabsItem
							id="tab-scheduleStudents"
							aria-controls="scheduleStudents"
							before={<Icon16EducationOutline/>}
							selected={activePanel === 'scheduleStudents'}
							onClick={() => setActivePanel('scheduleStudents')}
						>Расписание студентов</TabsItem>
						<TabsItem
							id="tab-scheduleTeachers"
							aria-controls="scheduleTeachers"
							before={<Icon16UserOutline/>}
							selected={activePanel === 'scheduleTeachers'}
							onClick={() => setActivePanel('scheduleTeachers')}
						>Расписание преподавателей</TabsItem>
						<TabsItem
							id="tab-information"
							aria-controls="information"
							before={<Icon16InfoOutline/>}
							selected={activePanel === 'information'}
							onClick={() => setActivePanel('information')}
						>О приложении</TabsItem>
					</HorizontalScroll>
				</Tabs>
			</Group>
			<View activePanel={activePanel}>
				<ScheduleStudents id="scheduleStudents" aria-labelledby="tab-scheduleStudents"/>
				<ScheduleTeachers id="scheduleTeachers" aria-labelledby="tab-scheduleTeachers"/>
				<Information id="information" aria-labelledby="tab-information"/>
			</View>
		</SplitCol>
	);
};

export default App;